import * as PIXI from "pixi.js";
import { dimensions, getCreateChipInput, onResize } from "../../utils";
import GUIWindow from "./component/gui_window";
import getTileTypes from "../tiles/tile_types";
import ButtonGroup from "./component/button_group";
import LineWrapLayout from "./layout/line_wrap_layout";
import config from "../../config";
import { GUIComponent, GUIComponentState } from "./component/gui_component";
import { getSprite } from "../sprites/sprite_loader";
import state, { setState, subscribe } from "../../state";
import { EditMode } from "../../utils/edit_mode";
import SelectorMenu from "./component/selector_menu";
var createToolBtn = function (spriteKey) {
    var toolHover = new PIXI.Graphics();
    toolHover.beginFill(config.colors.highlightTool);
    toolHover.drawRect(0, 0, config.menubarSize, config.menubarSize);
    toolHover.endFill();
    // toolHover.scale.x = 1 / btnData.hoverContainer.scale.x;
    // toolHover.scale.y = 1 / btnData.hoverContainer.scale.y;
    toolHover.alpha = 0.2;
    toolHover.zIndex = 200;
    var tool = new GUIComponent(0, 0, config.menubarSize, config.menubarSize, config.colors.menu);
    var defaultContainer = new PIXI.Container();
    var defaultSprite = getSprite(spriteKey);
    defaultSprite.width = tool.cWidth;
    defaultSprite.height = tool.cHeight;
    defaultContainer.addChild(defaultSprite);
    tool.setDefaultContainer(defaultContainer);
    var hoverContainer = new PIXI.Container();
    var hoverSprite = getSprite(spriteKey);
    hoverSprite.width = tool.cWidth;
    hoverSprite.height = tool.cHeight;
    hoverContainer.addChild(hoverSprite);
    hoverContainer.addChild(toolHover);
    tool.setHoverContainer(hoverContainer);
    tool.setState(GUIComponentState.DEFAULT);
    return tool;
};
var createToolSelector = function (onSelectionChange) {
    var selector = new GUIWindow(0, config.menubarSize, config.menubarSize, dimensions()[1] - config.menubarSize, config.colors.menu);
    var layout = new LineWrapLayout(config.menubarSize, config.menubarSize, 0);
    var cursorTool = createToolBtn("cursor");
    var panTool = createToolBtn("pan");
    var eraseTool = createToolBtn("eraser");
    var tilesTool = createToolBtn("tiles");
    var chipsTool = createToolBtn("chips");
    selector.setLayout(layout);
    selector.addChild(cursorTool);
    selector.addChild(panTool);
    selector.addChild(eraseTool);
    selector.addChild(tilesTool);
    selector.addChild(chipsTool);
    var selectedGraphics = new PIXI.Graphics();
    selectedGraphics.beginFill(config.colors.selectedTool);
    selectedGraphics.drawRect(0, 0, config.menubarSize, config.menubarSize);
    selectedGraphics.endFill();
    // toolHover.scale.x = 1 / btnData.hoverContainer.scale.x;
    // toolHover.scale.y = 1 / btnData.hoverContainer.scale.y;
    selectedGraphics.alpha = 0.2;
    selectedGraphics.zIndex = 200;
    var btnGroup = new ButtonGroup(selectedGraphics);
    btnGroup.addButton(cursorTool);
    btnGroup.addButton(panTool);
    btnGroup.addButton(eraseTool);
    btnGroup.addButton(tilesTool);
    btnGroup.addButton(chipsTool);
    btnGroup.onSelectionChange = onSelectionChange;
    btnGroup.setSelected(state.editMode);
    subscribe(["editMode"], function (e) {
        if (e.value === e.prevValue)
            return;
        btnGroup.setSelected(e.value);
    });
    return selector;
};
/**
 * Sets up application GUI
 *
 * @param app PIXI application
 * @param gridManager Grid Manager
 */
var initGUI = function (app, gridManager) {
    var selectorHeights = dimensions()[1] - config.menubarSize;
    /*
    MENU BAR
    */
    var menuBar = new GUIWindow(0, 0, dimensions()[0], config.menubarSize, config.colors.menu);
    /*
    TILE SELECTION
    */
    var tileSelector = new SelectorMenu(config.menubarSize, config.menubarSize, config.selectorWidth, selectorHeights, "Tiles", function (i, tileSize) {
        if (i >= getTileTypes(state.chipEditor).length)
            return null;
        var tileType = getTileTypes(state.chipEditor)[i];
        var tileOff = new tileType(0, 0);
        var tileOn = new tileType(0, 0);
        tileOn.signalActive = true;
        var defaultContainer = tileOff.getContainer(tileSize);
        var hoverContainer = tileOn.getContainer(tileSize);
        return {
            name: tileOff.label,
            defaultContainer: defaultContainer,
            hoverContainer: hoverContainer,
            selectable: true,
        };
    }, function (i) {
        return (gridManager.getGrid().selectedTileType = i);
    });
    tileSelector.visible = false;
    subscribe(["chipEditor"], function () {
        gridManager.getGrid().selectedTileType = -1;
        tileSelector.generateComponents();
    });
    /*
    CHIP SELECTION
    */
    var chipSelector = new SelectorMenu(config.menubarSize, config.menubarSize, config.selectorWidth, selectorHeights, "Chips", function (i, tileSize) {
        if (i > state.chips.length)
            return null;
        if (i === state.chips.length) {
            var defaultContainer_1 = new PIXI.Container();
            var defaultSprite = getSprite("add");
            defaultSprite.width = tileSize;
            defaultSprite.height = tileSize;
            defaultContainer_1.addChild(defaultSprite);
            var hoverContainer_1 = new PIXI.Container();
            var hoverSprite = getSprite("add");
            hoverSprite.width = tileSize;
            hoverSprite.height = tileSize;
            hoverContainer_1.addChild(hoverSprite);
            return {
                name: "New Chip",
                defaultContainer: defaultContainer_1,
                hoverContainer: hoverContainer_1,
                selectable: false,
                onClick: function () {
                    getCreateChipInput().then(function (values) {
                        if (!values)
                            return;
                        console.log(values);
                    });
                },
            };
        }
        var chip = state.chips[i];
        var genContainer = function () {
            var graphics = new PIXI.Graphics();
            graphics.beginFill(chip.color);
            graphics.drawRect(0, 0, 100, 100);
            graphics.endFill();
            graphics.width = tileSize;
            graphics.height = tileSize;
            var container = new PIXI.Container();
            container.addChild(graphics);
            return container;
        };
        var defaultContainer = genContainer();
        var hoverContainer = genContainer();
        return {
            name: chip.name,
            defaultContainer: defaultContainer,
            hoverContainer: hoverContainer,
            selectable: true,
        };
    }, function (i) { return (gridManager.getGrid().selectedTileType = i); });
    chipSelector.visible = false;
    subscribe(["chips"], function () {
        chipSelector.generateComponents();
    });
    /*
    TOOL SELECTION
    */
    var toolSelector = createToolSelector(function (i) {
        tileSelector.visible = false;
        chipSelector.visible = false;
        // state.editMode = i;
        setState({ editMode: i });
        switch (i) {
            case EditMode.TILE:
                // Tiles
                tileSelector.visible = true;
                break;
            case EditMode.CHIP:
                // Chips
                chipSelector.visible = true;
                break;
        }
    });
    app.stage.addChild(chipSelector);
    app.stage.addChild(tileSelector);
    app.stage.addChild(toolSelector);
    app.stage.addChild(menuBar);
    onResize(function () {
        var _a;
        var selectorHeights = dimensions()[1] - config.menubarSize;
        (_a = app.renderer).resize.apply(_a, dimensions());
        tileSelector.setSize(config.selectorWidth, selectorHeights);
        chipSelector.setSize(config.selectorWidth, selectorHeights);
        chipSelector.y = config.menubarSize;
        menuBar.setSize(dimensions()[0], config.menubarSize);
        toolSelector.setSize(toolSelector.width, dimensions()[1] - config.menubarSize);
    });
};
export default initGUI;
